// 個人資訊
export class Person {
    constructor (ch_name, en_name, address, address2, phone, email) {
        this.ch_name = ch_name;    // 中文姓名
        this.en_name = en_name;    // 英文姓名
        this.address = address;    // 地址
        this.address2 = address2;    // 地址2
        this.phone = phone;        // 電話
        this.email = email;        // email
    }
}

// 銀行資料
export class Payment {
    constructor (branch_title, branch_code, account_name, account_code) {
        this.branch_title = branch_title;    // 銀行名稱
        this.branch_code = branch_code;      // 銀行代碼
        this.account_name = account_name;    // 請款人銀行帳戶名稱
        this.account_code = account_code;    // 請款人銀行帳戶號碼
    }
}

// 請款單
export class Invoice {
    constructor (id, date, list, tax) {
        this.id = id;       // 請款單編號 #00
        this.date = date;   // 請款日期 (yyyy/mm/dd www.)
        this.list = list;   // 請款項目 (多筆資料): array
        this.tax = tax;     // 稅 (%)
    }
}

// 請款項目
export class InvoiceItem {
    constructor (des, type, hours, rate, amount, temp_des, temp_type, temp_hours, temp_rate, status_edit) {
        // 單向綁定
        this.des = des;        // 描述
        this.type = type;      // 類型
        this.hours = hours;    // 時數
        this.rate = rate;      // 時薪 (預設 300)
        this.amount = amount;  // 加總金額

        // 雙向綁定
        this.temp_des = temp_des;        // 描述
        this.temp_type = temp_type;      // 類型
        this.temp_hours = temp_hours;    // 時數
        this.temp_rate = temp_rate;      // 時薪 (預設 300)

        // 狀態
        this.status_edit = status_edit; // 編輯狀態 boolean
    }
}