<template>
    <section class="bill-generator">
        <!-- A4 尺寸 -->
        <section class="page">

            <!-- 儲存為 PDF 範圍 -->
            <div ref="paperTarget" class="paper-outer">
                
                <div class="paper">
                    <!-- Header -->
                    <nav class="paper-header">
                        <div class="header-left">
                            <div class="page-title">
                                <div class="title-image">
                                    <img src="@/assets/images/bill-generator/Invoice.png" alt="Invoice">
                                </div>
                                <h2 class="title-text">電子帳單</h2>
                            </div>
                        </div>
                        <div class="header-right">
                            <div class="logo-image">
                                <img src="@/assets/images/bill-generator/logo.png" alt="logo">
                            </div>
                        </div>
                    </nav>

                    <!-- Info -->
                    <div class="paper-detail">
                        <div class="detail-payer">
                            <p class="primary-detail">{{ data.payer.ch_name }} {{ data.payer.en_name }}</p>
                            <p class="secondary-detail">{{ data.payer.address }}<br>{{ data.payer.address2 }}</p>
                            <p class="secondary-detail">{{ data.payer.phone }}</p>
                            <p class="secondary-detail">{{ data.payer.email }}</p>
                        </div>
                        <div class="detail-payee">
                            <p class="primary-detail">{{ data.payee.ch_name }} {{ data.payee.en_name }}</p>
                            <p class="secondary-detail">{{ data.payee.address }}<br>{{ data.payee.address2 }}</p>
                            <p class="secondary-detail">{{ data.payee.phone }}</p>
                            <p class="secondary-detail">{{ data.payee.email }}</p>
                        </div>
                    </div>
                    
                    <!-- Body -->
                    <div class="paper-body">
                        <div class="invoice-table-title">
                            <p class="table-title">Invoice #{{ data.invoice_list.id }}</p>
                            <p class="table-title">{{ data.invoice_list.date }}</p>
                        </div>
                        <table class="invoice-table">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Description</th>
                                    <th>Hours</th>
                                    <th>Rate ($/Hr)</th>
                                    <th>Amount</th>
                                </tr>
                            </thead>
                            <tbody v-if="data.invoice_list.list">
                                <tr v-for="(item, index) of data.invoice_list.list" :key="index">
                                    <td>{{ index + 1 }}</td>
                                    <td>
                                        <p>{{ item.des }}</p>
                                        <p>{{ item.type }}</p>
                                    </td>
                                    <td>{{ item.hours }}</td>
                                    <td>{{ item.rate }}</td>
                                    <td>${{ moneyFormat(item.amount) }}</td>
                                </tr>
                            </tbody>
                            <tfoot class="total-row">
                                <tr>
                                    <th colspan="4">Sub Total</th>
                                    <td>${{ moneyFormat(data.subTotal) }}</td>
                                </tr>
                                <tr>
                                    <th colspan="4">
                                        Tax <span v-if="data.invoice_list.tax != 0">({{ data.invoice_list.tax }}%)</span>
                                    </th>
                                    <td>${{ moneyFormat( data.tax ) }}</td>
                                </tr>
                                <tr>
                                    <th colspan="4">Total</th>
                                    <td>${{ moneyFormat(data.total) }}</td>
                                </tr>
                                <tr>
                                    <th colspan="4">Amount Due (NTD)</th>
                                    <td>${{ moneyFormat(data.amountDue) }}</td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>

                    <!-- Payment -->
                    <div class="paper-footer">
                        <h2 class="footer-title">Payment Details</h2>

                        <table class="payment-table">
                            <thead>
                                <tr>
                                    <th>A/C with Bank</th>
                                    <th>Taishin International Bank (812)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th>Branch</th>
                                    <td>{{ data.payee_payment.branch_title }} ({{ data.payee_payment.branch_code }})</td>
                                </tr>
                                <tr>
                                    <th>Beneficiary's Name</th>
                                    <td>{{ data.payee_payment.account_name }}</td>
                                </tr>
                                <tr>
                                    <th>Beneficiary's A/C No.</th>
                                    <td>{{ data.payee_payment.account_code }}</td>
                                </tr>
                            </tbody>
                        </table>

                        <p class="notice">Payment terms (due on receipt, due in 7 days). Thank you!</p>
                    </div>
                </div>
                
                <p class="page-num">{ 1 / 1 }</p>

            </div>

        </section>

        <!-- 固定畫面區塊 -->
        <div class="fixed-wrap no-print">
        <!-- Modal Trigger -->
        <button type="button" class="btn btn-secondary edit-btn" data-bs-toggle="modal" data-bs-target="#exampleModal">編輯</button>

        <!-- Download PDF -->
        <button type="button" class="btn btn-secondary download-btn" @click="downloadPDF()">下載為PDF檔案</button>

        <!-- print -->
        <button type="button" class="btn btn-secondary print-btn" @click="printPage()">開啟Chrome列印模式</button>
        </div>


        <!-- Modal -->
        <div class="edit-modal modal fade no-print" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" data-bs-backdrop="static">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">編輯</h5>
                        <!-- <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button> -->
                    </div>
                    <div class="modal-body">
                        <form class="from-wrap">

                            <div class="container">
                                <div class="row">
                                    <div class="col-12 col-md-5">

                                        <div class="field-wrap">
                                            <h5 class="field-title">付款人資訊</h5>

                                            <div class="row">
                                                <div class="col-6">
                                                    <fieldset>
                                                        <label for="">中文姓名</label>
                                                        <input type="text" class="form-control" v-model="data.payer.ch_name">
                                                    </fieldset>
                                                </div>
                                                <div class="col-6">
                                                    <fieldset>
                                                        <label for="">英文姓名</label>
                                                        <input type="text" class="form-control" v-model="data.payer.en_name">
                                                    </fieldset>
                                                </div>
                                            </div>
                                            <div class="row">
                                                <div class="col-12">
                                                    <fieldset>
                                                        <label for="">地址</label>
                                                        <input type="text" class="form-control" v-model="data.payer.address">
                                                        <input type="text" class="form-control" v-model="data.payer.address2">
                                                    </fieldset>
                                                </div>
                                            </div>

                                            <div class="row">
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">電話</label>
                                                        <input type="text" class="form-control" v-model="data.payer.phone">
                                                    </fieldset>
                                                </div>
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">電子郵件</label>
                                                        <input type="text" class="form-control" v-model="data.payer.email">
                                                    </fieldset>
                                                </div>
                                            </div>
                                        </div>

                                        <div class="field-wrap">
                                            <h5 class="field-title">請款人資訊</h5>

                                            <div class="row">
                                                <div class="col-6">
                                                    <fieldset>
                                                        <label for="">中文姓名</label>
                                                        <input type="text" class="form-control" v-model="data.payee.ch_name">
                                                    </fieldset>
                                                </div>
                                                <div class="col-6">
                                                    <fieldset>
                                                        <label for="">英文姓名</label>
                                                        <input type="text" class="form-control" v-model="data.payee.en_name">
                                                    </fieldset>
                                                </div>
                                            </div>

                                            <div class="row">
                                                <div class="col-12">
                                                    <fieldset>
                                                        <label for="">地址</label>
                                                        <input type="text" class="form-control" v-model="data.payee.address">
                                                        <input type="text" class="form-control" v-model="data.payee.address2">
                                                    </fieldset>
                                                </div>
                                            </div>

                                            <div class="row">
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">電話</label>
                                                        <input type="text" class="form-control" v-model="data.payee.phone">
                                                    </fieldset>
                                                </div>
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">電子郵件</label>
                                                        <input type="text" class="form-control" v-model="data.payee.email">
                                                    </fieldset>
                                                </div>
                                            </div>

                                            <div class="row">
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">分行名稱</label>
                                                        <input type="text" class="form-control" v-model="data.payee_payment.branch_title">
                                                    </fieldset>
                                                </div>
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">分行代碼</label>
                                                        <input type="text" class="form-control" v-model="data.payee_payment.branch_code">
                                                    </fieldset>
                                                </div>
                                            </div>
                                            
                                            <div class="row">
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">請款人銀行名稱</label>
                                                        <input type="text" class="form-control" v-model="data.payee_payment.account_name">
                                                    </fieldset>
                                                </div>
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">請款人銀行帳號</label>
                                                        <input type="text" class="form-control" v-model="data.payee_payment.account_code">
                                                    </fieldset>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="col-12 col-md-7">

                                        <div class="field-wrap">
                                            <h5 class="field-title">請款單</h5>

                                            <div class="row">
                                                <div class="col-2">
                                                    <fieldset>
                                                        <label for="">編號#</label>
                                                        <input type="text" class="form-control" v-model="data.invoice_list.id">
                                                    </fieldset>
                                                </div>
                                                <div class="col-6">
                                                    <fieldset>
                                                        <label for="">日期</label>
                                                        <input type="text" class="form-control" v-model="data.invoice_list.date">
                                                    </fieldset>
                                                </div>
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">Tax (%)</label>
                                                        <input type="text" class="form-control" v-model="data.invoice_list.tax">
                                                    </fieldset>
                                                </div>
                                            </div>
                                        </div>

                                        <div class="field-wrap">
                                            <h5 class="field-title">請款項目</h5>

                                            <div class="row">
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">標題</label>
                                                        <input type="text" class="form-control" v-model="data.form.des">
                                                    </fieldset>
                                                </div>
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">時數</label>
                                                        <input type="text" class="form-control" v-model="data.form.hours">
                                                    </fieldset>
                                                </div>
                                            </div>
                                            <div class="row">
                                                <div class="col-8">
                                                    <fieldset>
                                                        <label for="">類型</label>
                                                        <input type="text" class="form-control" v-model="data.form.type">
                                                    </fieldset>
                                                </div>
                                                <div class="col-4">
                                                    <fieldset>
                                                        <label for="">時薪</label>
                                                        <input type="text" class="form-control" v-model="data.form.rate">
                                                    </fieldset>
                                                </div>
                                            </div>
                                            <div class="row">
                                                <div class="col-12">
                                                    <button type="button" class="btn btn-secondary btn-sm" @click="addItem()">新增</button>
                                                </div>
                                            </div>

                                        </div>

                                        <div class="field-wrap">
                                            <div class="row">
                                                <div class="col-12">
                                                    <table class="table">
                                                        <thead>
                                                            <tr>
                                                                <th>#</th>
                                                                <th>Description</th>
                                                                <th>Hours</th>
                                                                <th>Rate</th>
                                                                <th>
                                                                    Operate
                                                                    <button v-if="!data.status_sort" type="button" class="btn btn-secondary btn-sm" @click="enableSort('sort')">排序</button>
                                                                    <button v-if="data.status_sort" type="button" class="btn btn-secondary btn-sm" @click="enableSort('back')">返回</button>
                                                                </th>
                                                            </tr>
                                                        </thead>
                                                        <tbody v-if="data.invoice_list.list">
                                                            <tr v-for="(item, index) of data.invoice_list.list" :key="index">
                                                                <td>{{ index + 1 }}</td>
                                                                <td>
                                                                    <p>
                                                                        <template v-if="!item.status_edit">{{ item.des }}</template>
                                                                        <input v-if="item.status_edit" type="text" class="form-control" v-model="item.temp_des">
                                                                    </p>
                                                                    <p>
                                                                        <template v-if="!item.status_edit">{{ item.type }}</template>
                                                                        <input v-if="item.status_edit" type="text" class="form-control"  v-model="item.temp_type">
                                                                    </p>
                                                                </td>
                                                                <td>
                                                                    <template v-if="!item.status_edit">{{ item.hours }}</template>
                                                                    <input v-if="item.status_edit" type="text" class="form-control" v-model="item.temp_hours">
                                                                </td>
                                                                <td>
                                                                    <template v-if="!item.status_edit">{{ item.rate }}</template>
                                                                    <input v-if="item.status_edit" type="text" class="form-control" v-model="item.temp_rate">
                                                                </td>
                                                                <td>
                                                                    <div class="btn-wrap">

                                                                        <template v-if="!data.status_sort">
                                                                            <!-- 功能選擇 -->
                                                                            <template v-if="!item.status_edit">
                                                                                <button type="button" class="btn btn-secondary btn-sm" @click="editItem(index, 'edit')">編輯</button>
                                                                                <button type="button" class="btn btn-secondary btn-sm" @click="delItem(index)">刪除</button>
                                                                            </template>
                                                                            
                                                                            <!-- 編輯子選項 -->
                                                                            <template v-if="item.status_edit">
                                                                                <button type="button" class="btn btn-secondary btn-sm" @click="editItem(index, 'save')">儲存</button>
                                                                                <button type="button" class="btn btn-secondary btn-sm" @click="editItem(index, 'cancel')">取消</button>
                                                                            </template>
                                                                        </template>

                                                                        <!-- 排序子選項 -->
                                                                        <template v-if="data.status_sort">
                                                                            <button type="button" class="btn btn-secondary btn-sm" @click="sortItem(index, 'up')">上移</button>
                                                                            <button type="button" class="btn btn-secondary btn-sm" @click="sortItem(index, 'down')">下移</button>
                                                                        </template>
                                                                    </div>
                                                                </td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        </div>
                                        
                                    </div>
                                </div>
                            </div>
                            
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="saveToLocal()">儲存</button>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
// 匯入 html2pdf.js
import html2pdf from 'html2pdf.js';

// 匯入 Class
import { Person, Payment, Invoice, InvoiceItem } from '@/class/bill-generator';

// 匯入 json
import invoicesData from '@/assets/data/invoices.json';

import { ref, computed, nextTick, onMounted, reactive } from 'vue';

export default {
    name: 'BillGenerator',
    setup() {
        // 元素
        let paperTarget = ref(null);

        // 數據
        const data = reactive({
            payer: new Person(),   // 付款人 Person
            payee: new Person(),   // 請款人 Person
            payee_payment: new Payment(),  // 請款人帳戶 Payment
            invoice_list: new Invoice(),   // 請款清單 Invoice

            // 新增表單-暫存資料
            form: new InvoiceItem(),  // 請款項目

            status_sort: false, // 排序狀態 boolean
        })

        // 計算屬性

        // sub total = amount 全部加起來
        data.subTotal = computed(()=>{
            if ( !data.invoice_list.list ) return;

            let sum = 0;
            for ( let item of data.invoice_list.list ) {
                sum+=item.amount;
            }

            return sum;
        })

        // tax = sub total + tax(%)
        data.tax = computed(()=>{
            return (+data.subTotal) * ((+data.invoice_list.tax) / 100);
        })

        // total = sub total + tax
        data.total = computed(()=>{
            return (+data.subTotal) + (+data.tax);
        })

        // amount due = total - (discount)
        data.amountDue = computed(()=>{
            return data.total;
        })


        // 方法

        //-----------------------------------
        // Event
        //-----------------------------------

        // 設定預設值
        async function setDefaultData() {

            // 從 localStorage 取得之前存的資料
            let invoiceData = localStorage.getItem("invoices");

            let loadData = null;

            // 從 localStorage 取得
            if ( invoiceData ) {
                loadData = JSON.parse(invoiceData).invoices;

            // 從 寫死json 取得
            } else {
                loadData = invoicesData.invoices
            }


            // 付款人
            data.payer.ch_name = loadData.payer.ch_name;
            data.payer.en_name = loadData.payer.en_name;
            data.payer.address = loadData.payer.address;
            data.payer.address2 = loadData.payer.address2;
            data.payer.phone = loadData.payer.phone;
            data.payer.email = loadData.payer.email;

            // 請款人
            data.payee.ch_name = loadData.payee.ch_name;
            data.payee.en_name = loadData.payee.en_name;
            data.payee.address = loadData.payee.address;
            data.payee.address2 = loadData.payee.address2;
            data.payee.phone = loadData.payee.phone;
            data.payee.email = loadData.payee.email;

            // 請款人帳戶
            data.payee_payment.branch_title = loadData.payee_payment.branch_title;
            data.payee_payment.branch_code = loadData.payee_payment.branch_code;
            data.payee_payment.account_name = loadData.payee_payment.account_name;
            data.payee_payment.account_code = loadData.payee_payment.account_code;

            // 請款清單
            data.invoice_list.id = "00";
            data.invoice_list.tax = 0;
            data.invoice_list.list = [];

            // 製作當日日期
            let today = new Date();
            let www = today.toLocaleString('en-US', { weekday: 'short'} );
            let yyyy = (today.getFullYear()).toString();
            let mm = (today.getMonth()+1).toString().padStart(2, "0");
            let dd = (today.getDate()).toString().padStart(2, "0");

            data.invoice_list.date = `${yyyy}/${mm}/${dd} ${www}.`; // "2024/01/01 Mon."

            // 請款清單項目
            
            // 從 sessionStorage 取得之前存的資料
            let invoiceItemData = sessionStorage.getItem("invoicesItem");
            let data_item = null;

            // 從 sessionStorage 取得
            if ( invoiceItemData ) {
                data_item = JSON.parse(invoiceItemData).invoicesItem;

                for (let item of data_item) {
                    data.invoice_list.list.push(new InvoiceItem(item.des, item.type, item.hours, item.rate, item.amount));
                }
            
            // 從 寫死 取得
            } else {
                // for (let i=0; i < 4; i++) {
                //     il.list.push(new InvoiceItem("Synctify OMS", "Front End", 1.5, 300, 1.5*300));
                // }
            }


            // 新增表單-暫存資料
            data.form.rate = 300;
        }

        //-----------------------------------
        // Render
        //-----------------------------------

        // 貨幣格式化: 每三位數一個逗號
        function moneyFormat(num) {
            return num ? num.toLocaleString('en-US') : num;
        }

        // 控制排序按紐顯示
        function enableSort(type) {
            switch(type) {
                case 'sort':
                data.status_sort = true;
                    break;
                case 'back':
                data.status_sort = false;
                    break;
            }
        }


        //-----------------------------------
        // Binding
        //-----------------------------------

        // 新增項目
        function addItem() {
            // 新增一筆項目
            let ii = new InvoiceItem();
            ii.des = data.form.des;
            ii.type = data.form.type; 
            ii.hours = data.form.hours; 
            ii.rate = data.form.rate;
            ii.amount = data.form.hours * data.form.rate;
            data.invoice_list.list.push(ii);

            // 清空新增表單
            data.form.des = "";
            data.form.type = "";
            data.form.hours = "";
            data.form.rate = 300;
        }

        // 刪除項目
        function delItem(index) {
            let flag = window.confirm(`確定要刪除此筆項目嗎？`);
            if ( !flag ) return;
            data.invoice_list.list = data.invoice_list.list.filter((item, item_index) => item_index != index );
        }

        // 編輯項目
        function editItem(index, type) {

            switch(type) {
                // 編輯
                case 'edit':
                    // 新增暫存資料
                    data.invoice_list.list[index].temp_des = data.invoice_list.list[index].des;
                    data.invoice_list.list[index].temp_type = data.invoice_list.list[index].type;
                    data.invoice_list.list[index].temp_hours = data.invoice_list.list[index].hours;
                    data.invoice_list.list[index].temp_rate = data.invoice_list.list[index].rate;
                
                    // 更新狀態
                    data.invoice_list.list[index].status_edit = true;
                    break;

                // 儲存
                case 'save':
                    // 儲存暫存資料
                    data.invoice_list.list[index].des = data.invoice_list.list[index].temp_des;
                    data.invoice_list.list[index].type = data.invoice_list.list[index].temp_type;
                    data.invoice_list.list[index].hours = data.invoice_list.list[index].temp_hours;
                    data.invoice_list.list[index].rate = data.invoice_list.list[index].temp_rate;
                    data.invoice_list.list[index].amount = data.invoice_list.list[index].hours * data.invoice_list.list[index].rate;

                    // 更新狀態
                    data.invoice_list.list[index].status_edit = false;
                    break;

                // 取消
                case 'cancel':
                    // 捨棄暫存資料
                    data.invoice_list.list[index].temp_des = data.invoice_list.list[index].des;
                    data.invoice_list.list[index].temp_type = data.invoice_list.list[index].type;
                    data.invoice_list.list[index].temp_hours = data.invoice_list.list[index].hours;
                    data.invoice_list.list[index].temp_rate = data.invoice_list.list[index].rate;

                    // 更新狀態
                    data.invoice_list.list[index].status_edit = false;
                    break;
            }
        }

        // 排序成員
        function sortItem(index, type) {
            let index1 = index;
            let index2 = null;

            switch(type) {
                case 'up':
                    index2 = (index1 - 1) < 0 ? null : (index1 - 1);
                    break;
                case 'down':
                    index2 = (index1 + 1) > (data.invoice_list.list.length - 1) ? null : (index1 + 1);
                    break;
            }

            if ( index2 == null ) return;

            // 交換元素順序
            data.invoice_list.list[index1] = data.invoice_list.list.splice(index2, 1, data.invoice_list.list[index1])[0];
        }




        // 儲存至 local storage
        function saveToLocal() {            
            // 將資料存到 localStorage (關掉不會消失)
            let saveData = {
                payer: data.payer,
                payee: data.payee,
                payee_payment: data.payee_payment
            };
            localStorage.setItem("invoices", JSON.stringify({ invoices: saveData }));

            // 將資料存到 sessionStorage (關掉會消失)
            let items = []
            for (let item of data.invoice_list.list) {
                items.push(item);
            }
            sessionStorage.setItem("invoicesItem", JSON.stringify({ invoicesItem: items }));
        }


        // 下載 PDF 檔案
        function downloadPDF() {
            // 目前日期
            let today = new Date();
            let yy = (today.getFullYear()).toString().substr(2, 2); // 取後兩個字
            let mm = (today.getMonth()+1).toString().padStart(2, "0");
            let dd = (today.getDate()).toString().padStart(2, "0");

            // PDF 範圍
            let element = paperTarget.value;
            let x_offset = navigator.userAgent.indexOf('Mac OS X') != -1 ? 0 : -8.5; // Mac 不要調整

            var opt = {
                margin: 0,
                filename: `${yy}${mm}${dd}_Synctify_Design_Invoice.pdf`,
                image: { type: 'jpeg', quality: 1 },
                html2canvas: { scale: 3, scrollY: 0, scrollX: x_offset, useCORS: true },
            };
              
            // New Promise-based usage:
            html2pdf().set(opt).from(element).save();
              
            // Old monolithic-style usage:
            // html2pdf(element);

            
        }

        // 開啟列印模式
        function printPage() {
            var is_chrome = function () { return Boolean(window.chrome); }
            
            if( is_chrome() ) {
                window.print();
            }
        }



        // 生命週期鉤子
        onMounted(()=>{
            // 設定預設值
            setDefaultData();


            nextTick(()=>{
                // do stuff with the DOM

            })
        })


        return {
            data,
            paperTarget,

            moneyFormat,
            enableSort,
            addItem,
            delItem,
            editItem,
            sortItem,
            saveToLocal,
            downloadPDF,
            printPage,
        }
    }
}
</script>





